import React from 'react';
import PropTypes from 'prop-types';
import styled from 'styled-components';
import IconButton from 'views/IconButton/v1/IconButton';
import { motion, AnimatePresence } from "framer-motion";

const Modal = (props) => {
    
    return <AnimatePresence>
        {props.active && <Bg  key={props.id || "modal"} id={props.id || "modal"} className="z-ui" 
                initial={HIDDEN}
                animate={VISIBLE}
                exit={HIDDEN}
                >
        <Mod className={`bg-grad-pp ${props.className || ''}`} width={props.width} maxWidth={props.maxWidth} height={props.height} 
                color={props.color}>
            {props.children}
            {props.onClose && <Close>
                    <IconButton icon="icon-cross" color="text" 
                        bgColor="black" color="primary" 
                        onInput={props.onClose} />
                </Close>}
        </Mod>
    </Bg>}
    </AnimatePresence>
}

Modal.propTypes = {
    active: PropTypes.oneOfType([
        PropTypes.bool,
        PropTypes.number,
        PropTypes.string,
        PropTypes.object,
    ]),
    className: PropTypes.string,
    id: PropTypes.string,
    width: PropTypes.string,
    height: PropTypes.string,
    maxWidth: PropTypes.string,
    color: PropTypes.string,
    onClose: PropTypes.func
}

export default Modal;

const VISIBLE = {
    opacity: 1,
    y: 0,
    transition: {
        ease: "easeOut", duration: 0.5, delay: 0.3
    }
}
const HIDDEN = {
    opacity: 0,
    y: 100,
    transition: {
        ease: "easeOut", duration: 0.3
    }
}
/*
    <IconButton 
        className="sign-in-close"
        icon="icon-cross"
        onInput={cancelSignIn}
        color="primary"
        bgColor="black"
    />
*/
const Close = styled.div`
    position: absolute;
    top:-50px;
    left:0px;
    z-index: ${props => props.theme.zUi}; /* Sit on top */
`
const Bg = styled(motion.div)`
    height:100%;
    display: block;
    position: fixed;
    right:0; bottom:0;
    z-index: ${props => props.theme.zUi}; /* Sit on top */
    left: 0;
    top: 30px;
    background-color: rgba(0,0,0,0.8);
    overflow: hidden;
`
// pointer-events: ${props => props.active?'auto':'none'};  background-color: rgba(0,0,0,1);
const Mod = styled.div`
    box-sizing: border-box;
    width: ${props => props.width || '90%'};
    max-width:${props => props.maxWidth || '800px'};
    max-height: 80%;
    min-height: 300px;
    ${props => props.height?`height:${props.height};`:''}
    background-color: ${props => props.color? props.theme.getColor(props.theme, props.color, 'neutralD') : 'rgba(0,0,0,1)'};
    position: absolute;
    top:80px;
    left: 50%;
    margin: auto;
    padding: 10px 0px;
    transform: translateX(-50%);
    text-align: center;


    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items:stretch;
    
    .modal-buttons{
        float: right;
    }
   
`
//TODO Mod overflow-y needed so apple iphone 7 can scroll, but cant use select with it