import React, {useEffect, useState} from 'react';
import PropTypes from 'prop-types';
import {Placeholder, Wrapper, Box, Spinner} from './Loading.css.js';
import {sendError} from 'utilities/Measure.js';
import Text from 'views/Text/Text/v1/Text.js';

const Loading = (props) => {
    const [active, setActive] = useState(props.autoActive?true:false);
    useEffect(()=>{
        if(!props.autoActive){
            let timeout = setTimeout(()=>{
                setActive(true);
            }, props.delay || props.delay === 0?props.delay:2000);
            return ()=>{
                clearTimeout(timeout);
            }
        }

    }, [])
    useEffect(()=>{
        if(props.error){
            sendError('Error loading component ' + props.name, false);
        }
    }, [props.error])
    const renderAnimation = () => {
        if(props.spinner){
            return <Spinner  height={props.height}>
                <div className="orb" />
                <div className="orb" />
                <div className="orb" />
                <div className="orb" />
                <div className="orb" />
                <div className="orb" />
                <div className="orb" />
                <div className="orb" />
                {props.text && <Text className="spinner-text" fontSize="remS">{props.text}</Text>}
            </Spinner>
        }else{
            return <Wrapper error={props.error?true:false} height={props.height}>
                <Box error={props.error?true:false} className="loading-box" length={props.length} space={props.space} />
                <Box error={props.error?true:false} className="loading-box" length={props.length} space={props.space} />
                <Box error={props.error?true:false} className="loading-box" length={props.length} space={props.space}/>
                {props.error &&  <div className="error"><Text tag="p">error loading component</Text></div>}
                {props.text && <Text className="box-text text-center" fontSize="remS" margin="0px">{props.text}</Text>}
            </Wrapper>
        }

    }
    return (
        
        <Placeholder height={props.height} key={props.id || 'loading-wrapper'} id={props.id || 'loading-wrapper'}>
            {active && renderAnimation()}
        </Placeholder>
    )
}
Loading.propTypes = {
    spinner: PropTypes.bool,
    error: PropTypes.bool,
    length: PropTypes.string,
    height: PropTypes.string,
    space: PropTypes.string,
    delay: PropTypes.number
}
export default Loading;
